.page {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  grid-template-columns: minmax(320px, auto);
  min-height: 100%;
  min-width: 320px;
}

.page__header {
  width: 100%;
  z-index: 2;
}

.page__main {
  width: 100%;
  overflow: hidden;
  margin-top: -69px;
  z-index: 1;

  @include tablet {
    margin-top: -110px;
  }
}

.page__footer {
  width: 100%;
}
